body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

#App {
    width: 100vw;  
    height: 110vh;
    display: flex;
    justify-content: center;
}


#wrong {
    width: 135px;
    height: 20px;
    color: rgb(255, 255, 255);
    background-color: rgb(168, 28, 3, 0.5);
    border-radius: 5vw;
    padding: 5px 10px;
    margin: 5px 0;
    display: none;
}

#wrongText {
    font-size: 0.8rem;
    margin: 0;
    text-align: center;
}


#search {
    width: 210px;
    height: 40px;
    position: absolute;
    margin: 45px 0;
}

#findNewCity {
    width: 140px;
    height: 40px;
    font-size: 0.8rem;
    border: none;
    outline: none;
    color: rgb(0, 0, 0);
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 5vw 0 0 5vw;
    padding: 0 0 0 10px;
}

#findNewCity::placeholder {
    color: rgb(0, 0, 0);
}

#findCityButton {
    width: 60px;
    height: 40px;
    border: none;
    border-radius: 0 5vw 5vw 0;
    padding: 0;
    color: rgb(255, 255, 255);
    background-color: rgba(236, 110, 76, 0.7);
    margin-top: -1px;
}

#findCityButtonText {
    font-size: 1rem;
    margin: 0;
}



#weatherMainBox {
    width: 260px;
    height: 230px;
    color: rgb(70, 69, 69);
    background-color: rgba(177, 175, 175, 0.7);
    border-radius: 6vw;
    position: absolute;
    margin: 110px 0 0 0;
}

#cityName {
    width: 250px;
    font-size: 1rem;
    margin: 8px 0 0 0;
    text-align: center;
}

#faMapMarkerAlt {
    font-size: 0.9rem;
    display: inline-block;
}

#dateTime {
    width: 250px;
    font-size: 0.9rem;
    margin-top: 3px;
    text-align: center;
}

#mainInfo {
    width: 170px;
    display: inline-block;
}

#iconMain {
    height: 80px;
    margin-left: 15px;
    display: inline-block;
}

#mainTemp {
    width: 75px;
    font-size: 2.8rem;
    position: absolute;
    margin-top: 15px;
    display: inline-block;  
}

#relocation {
    font-size: 0.8rem;
    display: inline-block;
    position: absolute;
    margin: 45px 0 0 60px;
}

#secInfo {
    width: 90px;
    font-size: 0.75rem;
    display: inline-block;
    position: absolute;
    margin-top: 15px;
}

#humidity {
    position: absolute;
    margin-top: 4px;
    display: inline-block;
}

#humidityIcon {
    font-size: 1.1rem;
    display: inline-block;
    color: rgb(14, 206, 206);
}

#hourly {
    width: 260px;
    height: 100px;
}

#left {
    position: absolute;
    display: inline-block;
    margin: 39px 0 39px 5px;
    font-size: 1rem;
    padding-top: 2px;
}

#left:hover {
    padding-top: 2px;
    background-color: rgba(104, 107, 107, 0.7);
    border-radius: 5vw;
}

#right {
    position: absolute;
    display: inline-block;
    margin: 39px 0 39px 239px;
    padding-top: 2px;
    font-size: 1rem;
}

#right:hover {
    padding-top: 2px;
    background-color: rgba(104, 107, 107, 0.7);
    border-radius: 5vw;
}

#hourlyBox {
    width: 208px;
    height: 95px;
    display: inline-block;
    margin: 0 26px;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
}

.hourlyMiniBox {
    width: 38px;
    height: 80px;
    margin: 0 1.8px ;
    display: inline-block;
}

.dateHourly {
    font-size: 0.8rem;
    margin: 1px 0;
    text-align: center;
}

.icon {
    height: 2.4rem;
    margin: -5px 0;
}

.hourlyTemp {
    font-size: 0.8rem;
    margin: 0;
    text-align: center;
}

.humidityHourly {
    margin: 1px;
}

.humidityIcon1 {
    font-size: 0.7rem;
    display: inline-block;
    color: rgb(14, 206, 206);
}

.humidityText {
    font-size: 0.8rem;
    display: inline-block;
}


::-webkit-scrollbar {
    height: 10px;
}
  

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(29, 24, 24); 
    border-radius: 3vw;
}
   

::-webkit-scrollbar-thumb {
    background: rgb(29, 24, 24); 
    border-radius: 3vw;
}
  
::-webkit-scrollbar-thumb:hover {
    background: rgb(29, 24, 24); 
}



#dailyTime {
    width: 260px;
    height: 315px;
    color: rgb(70, 69, 69);
    background-color: rgba(177, 175, 175, 0.7);
    position: absolute;
    margin: 370px 0 0 0;
    border-radius: 5vw;
}

.dailyTimeBox {
    width: 250px;
    height: 42px;
    margin: 1.5px 5px;
}

.dayName {
    font-size: 1rem;
    margin: 12px 8px;
    position: absolute;
    display: inline-block;
}

.dailyHumidity {
    font-size: 0.9rem;
    display: inline-block;
    position: absolute;
    margin: 8px 0 0 60px ;
}

.dailyHumidityIcon {
    display: inline-block;
    position: absolute;
    margin: 5px 0 0 0;
    color: rgb(14, 206, 206);
    
}

.dailyHumidityText {
    display: inline-block;
    position: absolute;
    margin: 5px 0 0 15px;
}

.dailyIcon {
    height: 2.5rem;
    display: inline-block;
    position: absolute;
    margin: 0 0 0 120px;
}

.dailyMaxMinTemp {
    font-size: 0.8rem;
    display: inline-block;
    position: absolute;
    margin: 13px 0 0 180px;
}

